package com.mrmannwood.pathfinder

import kotlinx.browser.document
import kotlinx.browser.window
import org.w3c.dom.*
import kotlin.math.max
import kotlin.math.min

class CharacterSheetViewController(private val db: Any): ViewController {

    private val strengthAbilityScore = document.getElementById("strength_score") as HTMLParagraphElement
    private val strengthTempAdjustmentInput = document.getElementById("strength_temp_adjustment") as HTMLInputElement
    private val strengthModifier = document.getElementById("strength_modifier") as HTMLParagraphElement

    private val dexterityAbilityScore = document.getElementById("dexterity_score") as HTMLParagraphElement
    private val dexterityTempAdjustmentInput = document.getElementById("dexterity_temp_adjustment") as HTMLInputElement
    private val dexterityModifier = document.getElementById("dexterity_modifier") as HTMLParagraphElement

    private val constitutionAbilityScore = document.getElementById("constitution_score") as HTMLParagraphElement
    private val constitutionTempAdjustmentInput = document.getElementById("constitution_temp_adjustment") as HTMLInputElement
    private val constitutionModifier = document.getElementById("constitution_modifier") as HTMLParagraphElement

    private val intelligenceAbilityScore = document.getElementById("intelligence_score") as HTMLParagraphElement
    private val intelligenceTempAdjustmentInput = document.getElementById("intelligence_temp_adjustment") as HTMLInputElement
    private val intelligenceModifier = document.getElementById("intelligence_modifier") as HTMLParagraphElement

    private val wisdomAbilityScore = document.getElementById("wisdom_score") as HTMLParagraphElement
    private val wisdomTempAdjustmentInput = document.getElementById("wisdom_temp_adjustment") as HTMLInputElement
    private val wisdomModifier = document.getElementById("wisdom_modifier") as HTMLParagraphElement

    private val charismaAbilityScore = document.getElementById("charisma_score") as HTMLParagraphElement
    private val charismaTempAdjustmentInput = document.getElementById("charisma_temp_adjustment") as HTMLInputElement
    private val charismaModifier = document.getElementById("charisma_modifier") as HTMLParagraphElement

    private val maxHitPointsRow = document.getElementById("max_hit_points") as HTMLTableCellElement
    private val currentDamage = document.getElementById("current_damage") as HTMLTableCellElement
    private val currentHitPointsCell = document.getElementById("current_hit_points") as HTMLTableCellElement
    private val damageTable = document.getElementById("damage_table") as HTMLTableElement
    private val takeDamageButton = document.getElementById("take_damage") as HTMLButtonElement
    private val healDamageButton = document.getElementById("heal_damage") as HTMLButtonElement
    private val resetDamageButton = document.getElementById("reset_damage") as HTMLButtonElement

    private val totalACCell = document.getElementById("armor_class_total") as HTMLParagraphElement
    private val touchACCell = document.getElementById("armor_class_touch") as HTMLParagraphElement
    private val flatFootedACCell = document.getElementById("armor_class_flat_footed") as HTMLParagraphElement

    private val savesTable = document.getElementById("saves_bonus_table") as HTMLTableElement
    private val attackOptionsTable = document.getElementById("attack_options_table") as HTMLTableElement
    private val weaponsTable = document.getElementById("weapons_table") as HTMLTableElement
    private val baseAttackBonus = document.getElementById("base_attack_bonus") as HTMLParagraphElement
    private val skillsTable = document.getElementById("skills_table") as HTMLTableElement
    private val showAllSkillsInput = document.getElementById("show_all_skills") as HTMLInputElement
    private val equipmentTable = document.getElementById("equipment_table") as HTMLTableElement

    private val characterRepository = CharacterRepository.get(db)

    private val attackOptions = mutableMapOf<String, Triple<String, Any, HTMLElement>>()

    init {
        listOf(
            strengthTempAdjustmentInput,
            dexterityTempAdjustmentInput,
            constitutionTempAdjustmentInput,
            intelligenceTempAdjustmentInput,
            wisdomTempAdjustmentInput,
            charismaTempAdjustmentInput,
            showAllSkillsInput
        ).forEach {
            it.addEventListener("change", { updateUI() })
        }
        maxHitPointsRow.addEventListener("click", { showHpDetails() })
        takeDamageButton.addEventListener("click", {
            val damage = window.prompt("Damage","0")?.toIntOrNull() ?: return@addEventListener
            if (damage <= 0) return@addEventListener
            damageTable.insertRow(-1).apply {
                insertCell(-1).innerText = damage.toString()
                insertCell(-1)
            }
            updateUI()
        })
        healDamageButton.addEventListener("click", {
            val healing = window.prompt("Healing","0")?.toIntOrNull() ?: return@addEventListener
            if (healing <=  0) return@addEventListener
            val damage = (currentDamage.innerText.toIntOrNull() ?: 0)
            damageTable.insertRow(-1).apply {
                insertCell(-1)
                insertCell(-1).innerText = (if (healing > damage) damage else healing).toString()
            }
            updateUI()
        })
        resetDamageButton.addEventListener("click", {
            for(idx in 1 until damageTable.rows.length) {
                damageTable.deleteRow(1)
            }
            updateUI()
        })

    }

    private lateinit var character: Character

    override fun onEnter() {
        characterRepository.readCharacter().then { setCharacter(it) }.then { updateUI() }
    }

    override fun onExit() { }

    private fun setCharacter(char: Character) {
        character = char

        strengthAbilityScore.innerText = character.abilityScores.strength.getScore().toString()
        dexterityAbilityScore.innerText = character.abilityScores.dexterity.getScore().toString()
        constitutionAbilityScore.innerText = character.abilityScores.constitution.getScore().toString()
        intelligenceAbilityScore.innerText = character.abilityScores.intelligence.getScore().toString()
        wisdomAbilityScore.innerText = character.abilityScores.wisdom.getScore().toString()
        charismaAbilityScore.innerText = character.abilityScores.charisma.getScore().toString()
        baseAttackBonus.innerText = character.baseAttackBonus.joinToString(" ")
    }

    private fun createState(): State  {
        var state = State(
            character = character,
            equippedWeapon = character.weapons[0],
            damage = currentDamage.innerText.toIntOrNull() ?: 0,
            attacks = character.baseAttackBonus,
            inputs = attackOptions.values.associate { it.first to it.second },
        )
        state = calculateAndShowAbilityModifiers(state)
        state = calculateAndShowHitPoints(state)
        return state
    }

    private fun updateUI() {
        val state = createState()
        showArmorClass(state)
        showSaves(state)
        showAttackOptions(state)
        showWeapons(state)
        calculateAndShowSkills(state)
        showEquipment(state)
    }

    private fun calculateAndShowAbilityModifiers(state: State): State {
        val makeModifierScore: (HTMLInputElement) -> AbilityScore = { element ->
            AbilityScore(listOf(AbilityScoreComponent("modifier", ComponentType.TEMP_MODIFIER, element.value.toInt())))
        }
        return state.update(
            character = Character(
                characterDef = character.characterDef,
                equippedEquipment = character.equippedEquipment,
                unequippedEquipment = character.unequippedEquipment,
                abilityScoreModifiers = AbilityScores(
                    strength = makeModifierScore(strengthTempAdjustmentInput),
                    dexterity = makeModifierScore(dexterityTempAdjustmentInput),
                    constitution = makeModifierScore(constitutionTempAdjustmentInput),
                    intelligence = makeModifierScore(intelligenceTempAdjustmentInput),
                    wisdom = makeModifierScore(wisdomTempAdjustmentInput),
                    charisma = makeModifierScore(charismaTempAdjustmentInput)
                )
            ).also { char ->
                strengthModifier.innerText = char.abilityScores.strength.getModifier().toString()
                dexterityModifier.innerText = char.abilityScores.dexterity.getModifier().toString()
                constitutionModifier.innerText = char.abilityScores.constitution.getModifier().toString()
                intelligenceModifier.innerText = char.abilityScores.intelligence.getModifier().toString()
                wisdomModifier.innerText = char.abilityScores.wisdom.getModifier().toString()
                charismaModifier.innerText = char.abilityScores.charisma.getModifier().toString()
            }
        )
    }

    private fun calculateAndShowHitPoints(state: State): State {
        val maxHp = state.character.maxHp()
        var damage = 0
        var healing = 0
        for(idx in 1 until damageTable.rows.length) {
            val row = (damageTable.rows[idx] as HTMLTableRowElement).cells
            damage += (row[0] as HTMLTableCellElement).innerText.toIntOrNull() ?: 0
            healing += (row[1] as HTMLTableCellElement).innerText.toIntOrNull() ?: 0
        }

        val currDamage = damage - healing

        maxHitPointsRow.innerText = maxHp.toString()
        currentDamage.innerText = currDamage.toString()
        currentHitPointsCell.innerText = (maxHp - currDamage).toString()

        return state.update(damage = currDamage)
    }

    private fun showArmorClass(state: State) {
        val acBonuses = state.character.grants.filterIsInstance<Grant.GrantsArmorClassBonus>().map { it.getArmorClassBonus() }
        val dexBonus = Bonus(AbilityScoreBonus.DEXTERITY, "Dex") {
            val maxDexBonus = state.character.grants.filterIsInstance<Grant.GrantsMaxDexPenalty>().maxOfOrNull { it.getMaxDex() }
            val dex = state.character.abilityScores.dexterity.getModifier()
            min(maxDexBonus ?: Int.MAX_VALUE, dex)
        }

        totalACCell.innerText = (Bonus.getActiveBonuses(acBonuses).sumOf { it.bonus() } + dexBonus.bonus()).toString()
        touchACCell.innerText = (
                Bonus.getActiveBonuses(
                    acBonuses.filter { !((it.type as? ACBonusType)?.ignoreOnTouch ?: false) }
                ).sumOf { it.bonus() } + dexBonus.bonus())
            .toString()
        flatFootedACCell.innerText = (
                Bonus.getActiveBonuses(
                    acBonuses.filter { it.type !== ACBonusType.DODGE }
                ).sumOf { it.bonus() })
            .toString()
    }

    private fun showSaves(state: State) {
        while(savesTable.rows.length > 0) { savesTable.deleteRow(0) }

        listOf(
            Triple("Fortitude", state.character.saves.fortitudeSave, state.character.abilityScores.constitution),
            Triple("Reflex", state.character.saves.reflexSave, state.character.abilityScores.dexterity),
            Triple("Will", state.character.saves.willSave, state.character.abilityScores.wisdom),
        ).forEach { (name, saves, ability) ->
            savesTable.insertRow(-1).apply {
                val total = (Bonus.getActiveBonuses(saves).sumOf { it.bonus() } + ability.getModifier())
                (insertCell(-1) as HTMLTableCellElement).innerText = name
                (insertCell(-1) as HTMLTableCellElement).innerText = total.toString()
                addEventListener("click", {
                    showSaveDetails(name, saves, ability, total)
                })
            }
        }
    }

    private fun calculateAndShowSkills(state: State) {
        while(skillsTable.rows.length > 0) { skillsTable.deleteRow(0) }
        val header = skillsTable.createTHead()
        val hRow = header.insertRow(-1) as HTMLTableRowElement
        listOf("Bonus", "Name").forEach {
            hRow.insertCell(-1).innerHTML = "<b>$it</b>"
        }

        val showAllSkills = showAllSkillsInput.checked

        calculateStatDetails(state) { showAllSkills || !it.isPrototype }
            .filter { showAllSkills || it.totalBonus != null }
            .forEach { breakdown ->
                val row = skillsTable.insertRow(-1)
                (row.insertCell(-1) as HTMLTableCellElement).innerText = breakdown.totalBonus?.toString() ?: "X"
                (row.insertCell(-1) as HTMLTableCellElement).innerText = breakdown.skillName
                if (!breakdown.skill.isPrototype) {
                    row.addEventListener("click", {
                        showStatDetails(breakdown)
                    })
                }
            }
    }

    private fun showAttackOptions(state: State) {
        while(attackOptionsTable.rows.length > 0) { attackOptionsTable.deleteRow(0) }

        // range to target
        attackOptionsTable.insertRow(-1).apply {
            (insertCell(-1) as HTMLTableCellElement).apply {
                val inputId = "range_to_target_id"
                val option = InputNames.RANGE_TO_TARGET.displayName
                appendChild(
                    attackOptions.getOrPut(inputId) {
                        Triple(
                            option,
                            false,
                            (document.createElement("input") as HTMLInputElement).apply {
                                id = inputId
                                type = "number"
                                step = "5"
                                addEventListener("change", {
                                    attackOptions[option] = Triple(option, value.toInt(), this)
                                    updateUI()
                                })
                            }
                        )
                    }.third
                )
                appendChild(
                    (document.createElement("label") as HTMLLabelElement).apply {
                        htmlFor = inputId
                        innerText = option
                    }
                )
            }
        }

        // combat maneuvers
        mutableListOf(InputNames.WIELDING_TWO_HANDED.displayName).apply {
            addAll(state.character.grants.filter { it.optional() }.map { it.name })
        }.forEach { option ->
            attackOptionsTable.insertRow(-1).apply {
                (insertCell(-1) as HTMLTableCellElement).apply {
                    val inputId = "${option}_id"
                    appendChild(
                        attackOptions.getOrPut(inputId) {
                            Triple(
                                option,
                                false,
                                (document.createElement("input") as HTMLInputElement).apply {
                                    id = inputId
                                    type = "checkbox"
                                    addEventListener("change", {
                                        attackOptions[option] = Triple(option, checked, this)
                                        updateUI()
                                    })
                                }
                            )
                        }.third
                    )
                    appendChild(
                        (document.createElement("label") as HTMLLabelElement).apply {
                            htmlFor = inputId
                            innerText = option
                        }
                    )
                }
            }
        }
    }

    private fun showWeapons(state: State) {
        while(weaponsTable.rows.length > 0) { weaponsTable.deleteRow(0) }

        state.character.weapons.forEach { weapon ->
            var s = state.update(equippedWeapon = weapon)
            state.character.grants.filterIsInstance<Grant.ModifiesBaseAttackBonuses>().forEach {
                s = it.modifyBabs(s)
            }

            (weaponsTable.insertRow(-1)).apply {
                (insertCell(-1) as HTMLTableCellElement).innerText = weapon.name
                (insertCell(-1) as HTMLTableCellElement).apply {
                    innerText = calculateAttackBonus(s)
                    addEventListener("click", {
                        showAttackBonusDetails(s)
                    })
                }
                (insertCell(-1) as HTMLTableCellElement).innerText = if(weapon.criticalRange == 20) {
                    weapon.criticalRange.toString()
                } else {
                    "${weapon.criticalRange}-20"
                } + "/x${weapon.criticalMultiplier}"
            }

            (weaponsTable.insertRow(-1)).apply {
                (insertCell(-1) as HTMLTableCellElement).innerText = weapon.type.toString().first().toString()
                (insertCell(-1) as HTMLTableCellElement).innerText = weapon.rangeIncrement?.toString() ?: ""
                (insertCell(-1) as HTMLTableCellElement).appendChild(
                    (document.createElement("input") as HTMLInputElement).apply {
                        type = "number"
                        step = "1"
                        value = "1"
                    }
                )
                (insertCell(-1) as HTMLTableCellElement).apply {
                    innerText = calculateDamage(s).toString()
                    addEventListener("click", {
                        showDamageDetails(s)
                    })
                }
            }
        }
    }

    private fun showEquipment(state: State) {
        while(equipmentTable.rows.length > 0) { equipmentTable.deleteRow(0) }

        /*
        val header = skillsTable.createTHead()
        val hRow = header.insertRow(-1) as HTMLTableRowElement
        listOf("Bonus", "Name").forEach {
            hRow.insertCell(-1).innerHTML = "<b>$it</b>"
        }
         */
        equipmentTable.createTHead().apply {
            (insertRow(-1) as HTMLTableRowElement).apply {
                listOf(
                    "Equipped", "Item", "Weight", "Price"
                ).forEach { insertCell(-1).innerText = it }
            }
        }

        (state.character.equippedEquipment + state.character.unequippedEquipment).forEach { equipment ->
            equipmentTable.insertRow(-1).apply {
                (insertCell(-1) as HTMLTableCellElement).apply {
                    val inputId = "${equipment.name}_equipped"
                    val equipped = state.character.equippedEquipment.contains(equipment)
                    appendChild((document.createElement("label") as HTMLLabelElement).apply {
                        htmlFor = inputId
                    })
                    appendChild((document.createElement("input") as HTMLInputElement).apply {
                        id = inputId
                        type = "checkbox"
                        checked = equipped
                        addEventListener("change", {
                            val equippedEquipment = state.character.equippedEquipment.toMutableList()
                            val unequippedEquipment = state.character.unequippedEquipment.toMutableList()
                            if (equipped) {
                                equippedEquipment.remove(equipment)
                                unequippedEquipment.add(equipment)
                            } else {
                                equippedEquipment.add(equipment)
                                unequippedEquipment.remove(equipment)
                            }
                            setCharacter(
                                Character(
                                    characterDef = state.character.characterDef,
                                    equippedEquipment = equippedEquipment,
                                    unequippedEquipment = unequippedEquipment,
                                    abilityScoreModifiers = state.character.abilityScoreModifiers
                                )
                            )
                            updateUI()
                        })
                    })
                }
                (insertCell(-1) as HTMLTableCellElement).innerText = equipment.name
                (insertCell(-1) as HTMLTableCellElement).innerText = weightToPounds(equipment.weight).toString()
                (insertCell(-1) as HTMLTableCellElement).innerText = costToString(equipment.cost)
            }
        }
    }

    private fun calculateAttackBonus(state: State): String {
        val attacks = StringBuilder()
        getAllAttackBonuses(state).forEachIndexed { idx, attack ->
            if (idx > 0) attacks.append(" / ")
            attacks.append(attack.sumOf { bonus -> bonus.bonus(state) })
        }
        return attacks.toString()
    }

    private fun calculateDamage(state: State): DieExpression {
        var total = DieExpression.modifier(0)
        getAllDamageBonuses(state).forEach {
            it.bonus(state)?.let { bonus ->
                total = total.add(bonus)
            }
        }
        return total
    }

    private fun showHpDetails() {
        while(modalTable.rows.length > 0) { modalTable.deleteRow(0) }

        (modalTable.createTHead().insertRow(-1) as HTMLTableRowElement).apply {
            listOf("Bonus", "Source").forEach { insertCell(-1).innerText = it }
        }
        character.hp.forEach { bonus ->
            modalTable.insertRow(-1).apply {
                (insertCell(-1) as HTMLTableCellElement).innerText = bonus.bonus().toString()
                (insertCell(-1) as HTMLTableCellElement).innerText = bonus.source
            }
        }

        modal.style.display = "block"
    }

    private fun showSaveDetails(
        name: String, saves: List<Bonus>, abilityMod: AbilityScore, total: Int) {
        while(modalTable.rows.length > 0) { modalTable.deleteRow(0) }

        (modalTable.createTHead().insertRow(-1) as HTMLTableRowElement).apply {
            mutableListOf("Saving Throw", "Total").apply {
                addAll(saves.map { it.source })
                add("Ability Modifier")
            }.forEach { insertCell(-1).innerText = it }
        }

        modalTable.insertRow(-1).apply {
            (insertCell(-1) as HTMLTableCellElement).innerText = name
            (insertCell(-1) as HTMLTableCellElement).innerText = total.toString()
            saves.map { it.bonus() }.forEach { save ->
                (insertCell(-1) as HTMLTableCellElement).innerText = save.toString()
            }
            (insertCell(-1) as HTMLTableCellElement).innerText = abilityMod.getModifier().toString()
        }

        modal.style.display = "block"
    }

    private fun showAttackBonusDetails(state: State) {
        while(modalTable.rows.length > 0) {
            modalTable.deleteRow(0)
        }

        getAllAttackBonuses(state).forEachIndexed { babIdx, attack ->
            var total = 0
            attack.forEachIndexed { idx, component ->
                val bonus = component.bonus(state)
                total += bonus

                if (babIdx == 0) {
                    modalTable.insertRow(-1)
                } else {
                    modalTable.rows[idx] as HTMLTableRowElement
                }.apply{
                    (insertCell(-1) as HTMLTableCellElement).innerText = component.name
                    (insertCell(-1) as HTMLTableCellElement).innerText = bonus.toString()
                }
            }
            if (babIdx == 0) {
                modalTable.insertRow(-1)
            } else {
                modalTable.rows[modalTable.rows.length - 1] as HTMLTableRowElement
            }.apply{
                (insertCell(-1) as HTMLTableCellElement).innerText = "Total"
                (insertCell(-1) as HTMLTableCellElement).innerText = total.toString()
            }
        }

        modal.style.display = "block"
    }

    private fun showDamageDetails(state: State) {
        while(modalTable.rows.length > 0) {
            modalTable.deleteRow(0)
        }

        var total = DieExpression.modifier(0)
        getAllDamageBonuses(state).forEach { component ->
            modalTable.insertRow(-1).apply {
                (insertCell(-1) as HTMLTableCellElement).innerText = component.name
                val bonus = component.bonus(state)?.let {
                    total = total.add(it)
                    it.toString()
                } ?: run { "" }
                (insertCell(-1) as HTMLTableCellElement).innerText = bonus
            }
        }
        modalTable.insertRow(-1).apply{
            (insertCell(-1) as HTMLTableCellElement).innerText = "Total"
            (insertCell(-1) as HTMLTableCellElement).innerText = total.toString()
        }

        modal.style.display = "block"
    }

    private fun showStatDetails(breakdown: SkillBreakdown) {
        while(modalTable.rows.length > 0) {
            modalTable.deleteRow(0)
        }
        val header = modalTable.createTHead()
        val hRow = header.insertRow(-1) as HTMLTableRowElement

        listOf("Class Skill", "Name", "Training Required", "Bonus", "Ability", "Mod", "Ranks","Misc Value", "Misc Source").forEach {
            hRow.insertCell(-1).innerHTML = "<b>$it</b>"
        }

        modalTable.insertRow(-1).apply {
            (insertCell(-1) as HTMLTableCellElement).innerText = breakdown.classSkillSource?.joinToString("\n") { it } ?: ""
            (insertCell(-1) as HTMLTableCellElement).innerText = "${breakdown.skillName}${if (breakdown.skill.isPrototype) "___" else ""}"
            (insertCell(-1) as HTMLTableCellElement).innerText = if (breakdown.requiresTraining) "Yes" else "No"
            (insertCell(-1) as HTMLTableCellElement).innerText = breakdown.totalBonus?.toString() ?: "X"
            (insertCell(-1) as HTMLTableCellElement).innerText = breakdown.abilityName
            (insertCell(-1) as HTMLTableCellElement).innerText = breakdown.abilityMod.toString()
            (insertCell(-1) as HTMLTableCellElement).innerText = breakdown.ranks.toString()
            (insertCell(-1) as HTMLTableCellElement).innerText = breakdown.misc.joinToString("\n") { it.second.toString() }
            (insertCell(-1) as HTMLTableCellElement).innerText = breakdown.misc.joinToString("\n") { it.first }
       }

        modal.style.display = "block"
    }

    private fun getAllAttackBonuses(state: State): List<List<AttackBonusComponent>> {
        val allGrants = mutableListOf<Grant>().also {
            it.addAll(state.character.grants)
            it.addAll(state.equippedWeapon.grants)
        }
        val attackBonusGrants = allGrants.filterIsInstance<Grant.GrantsAttackBonus>()
        return state.attacks.map { bab ->
            attackBonusGrants.mapNotNull{ it.getAttackBonus(state) }
                .toMutableList()
                .also {
                    if (/* is ranged attack */ state.equippedWeapon.category == WeaponDef.Category.RANGED) {
                        it.add(RANGE_INCREMENT_COMPONENT)
                    }
                    it.add(
                        0,
                        if (state.equippedWeapon.category == WeaponDef.Category.RANGED ||
                            allGrants.filterIsInstance<Grant.GrantsDexterityBonusInsteadOfStrengthOnAttack>().find { it.shouldUseDex(state) } != null
                        ) {
                            AttackBonusComponent("Dexterity") { state.character.abilityScores.dexterity.getModifier() }
                        } else {
                            AttackBonusComponent("Strength") { state.character.abilityScores.strength.getModifier() }
                        }
                    )
                    it.add(0, AttackBonusComponent("BAB") { bab })
                }
                .toList()
        }
    }

    private fun getAllDamageBonuses(state: State): List<DamageComponent> {
        return mutableListOf<Grant>().also {
            it.addAll(state.character.grants)
            it.addAll(state.equippedWeapon.grants)
        }.filterIsInstance<Grant.GrantsDamageBonus>()
            .mapNotNull { it.getDamageBonus(state) }
            .toMutableList()
            .also {
                it.add(DamageComponent(state.equippedWeapon.definition.name) { state.equippedWeapon.damage })
                if (state.equippedWeapon.category != WeaponDef.Category.RANGED) {
                    it.add(MELEE_STRENGTH_COMPONENT)
                }
            }.toList()
    }

    private fun calculateStatDetails(state: State, filter: (Skill) -> Boolean = { true }): List<SkillBreakdown> {
        return SKILL_DEFINITIONS.map { skillDef ->
            state.character.skills.filter { filter(it.key) }.filter { it.key.name.startsWith(skillDef.name) }.ifEmpty { null } ?.let {
                it.map {
                    val skill = it.key
                    val bonuses = Bonus.getActiveBonuses(it.value)

                    SkillBreakdown(
                        skill = skill,
                        abilityMod = when(skill.abilityName) {
                            "STR" -> state.character.abilityScores.strength.getModifier()
                            "DEX" -> state.character.abilityScores.dexterity.getModifier()
                            "CON" -> state.character.abilityScores.constitution.getModifier()
                            "INT" -> state.character.abilityScores.intelligence.getModifier()
                            "WIS" -> state.character.abilityScores.wisdom.getModifier()
                            "CHA" -> state.character.abilityScores.charisma.getModifier()
                            else -> throw Exception("Unknown ability name: $skill")
                        },
                        ranks = bonuses.filter { it.type == InnateBonus }.sumOf { it.bonus() },
                        misc = bonuses.filter { it.type != InnateBonus }.map { it.source to it.bonus() }
                    )
                }
            }
        }
            .filterNotNull()
            .flatten()
    }

    data class SkillBreakdown(
        val skill: Skill,
        val abilityMod: Int,
        val ranks: Int,
        val misc: List<Pair<String, Int>>
    ) {

        val skillName = skill.name
        val abilityName = skill.abilityName
        val classSkillSource = skill.classSkillSource
        val isClassSkill = classSkillSource != null
        val requiresTraining = skill.requiresTraining

        val totalBonus: Int? = if (ranks < 1 && requiresTraining) {
            null
        } else {
            (if (isClassSkill && ranks > 0) 3 else 0) + abilityMod + ranks + misc.sumOf { it.second }
        }
    }
}