package com.mrmannwood.pathfinder

import kotlin.js.Json

enum class ACBonusType(
    val ignoreOnTouch: Boolean = false,
    private val stack: Boolean = false
): Bonus.Type {
    ARMOR(ignoreOnTouch = true),
    SHIELD(ignoreOnTouch = true),
    DEX,
    ENHANCEMENT,
    DEFLECTION,
    NATURAL(ignoreOnTouch = true),
    DODGE(true);

    override fun stacks(): Boolean = stack
}

data class ArmorDef(
    val name: String,
    val link: String,
    val type: Type,
    val cost: Int,
    val weight: Int,
    val armorClassBonus: Int,
    val maxDexBonus: Int?,
    val armorCheckPenalty: Int,
    val arcaneSpellFailureChance: Int,
    val speed30ft: Int,
    val speed20ft: Int
) {
    enum class Type {
        LIGHT, MEDIUM, HEAVY
    }

    companion object {
        private fun unknownArmorProperty(propertyName: String, propertyValue: Any?): Nothing {
            throw Exception("Unknown armor $propertyName: $propertyValue")
        }
        private fun convertType(type: String): Type {
            val t = type.uppercase()
            return Type.values().firstOrNull { it.name == t } ?: unknownArmorProperty("type", type)
        }
        fun fromJson(json: Json): ArmorDef {
            val parser = JsonParser(json)
            return ArmorDef(
                name = parser.parse("name") { it as String },
                link = parser.parse("link") { it as String },
                type = parser.parse("type") { convertType(it as String) },
                cost = parser.parse("cost") { it as Int },
                weight = parser.parse("weight") { it as Int },
                armorClassBonus = parser.parse("acBonus") { it as Int },
                maxDexBonus = parser.parse("maxDexBonus") { it as? Int? },
                armorCheckPenalty = parser.parse("acPenalty") { it as? Int? ?: 0 },
                arcaneSpellFailureChance = parser.parse("arcaneSpellFailure") { it as? Int? ?: 0 },
                speed30ft = parser.parse("speed30ft") { it as? Int? ?: 30 },
                speed20ft = parser.parse("speed20ft") { it  as? Int? ?: 20},
            )
        }
    }
}

class Armor(
    val definition: ArmorDef,
    val size: Size,
    override val cost: Int = definition.cost,
    override val weight: Int = computeWeight(definition.weight, size),
    grants: List<Grant> = emptyList()
): Equipment {
    companion object {
        private fun computeWeight(weight: Int, size: Size): Int {
            return when (size) {
                Size.SMALL -> weight / 2
                Size.MEDIUM -> weight
                Size.LARGE_LONG -> weight * 2
                Size.LARGE_TALL -> weight * 2
                else -> throw Exception("Unable to make armor for size $size")
            }
        }
        private fun computeGrants(definition: ArmorDef, grants: List<Grant>): List<Grant> {
            val grants = grants.toMutableList()
            definition.maxDexBonus?.let { maxDexBonus ->
                grants.add(object : Grant.GrantsMaxDexPenalty {
                    override val name = "Max Dex Penalty"
                    override val type: Grant.Type = Grant.Type.ARMOR
                    override fun getMaxDex() = maxDexBonus
                })
            }
            grants.add(object : Grant.GrantsArmorCheckPenalty {
                override val name = "Armor Check Penalty"
                override val type: Grant.Type = Grant.Type.ARMOR
                override fun getSkillPenalties(): Map<SkillDefinition, Bonus> {
                    return SKILL_DEFINITIONS.filter { listOf("DEX", "STR").contains(it.abilityName) }
                        .associateWith { Bonus(ACBonusType.ARMOR, name) { definition.armorCheckPenalty } }
                }

            })
            grants.add(object : Grant.GrantsArmorClassBonus {
                override val name = definition.name
                override val type: Grant.Type = Grant.Type.ARMOR
                override fun getArmorClassBonus() = Bonus(ACBonusType.ARMOR, name) { definition.armorClassBonus }
            })
            return grants
        }
        private fun computeName(definition: ArmorDef, grants: List<Grant>): String {
            return grants.filterIsInstance<Grant.GrantsNamePrefix>().joinToString(" ") { it.getPrefix() } +
                    " " + definition.name + " " +
                    grants.filterIsInstance<Grant.GrantsNameSuffix>().joinToString(" ") { it.getSuffix() }
        }
    }

    override val grants: List<Grant> = computeGrants(definition, grants)
    override val name: String = computeName(definition, grants)
    val arcaneSpellFailureChance = definition.arcaneSpellFailureChance

    fun getSpeed(characterSpeed: Int): Int {
        return when (characterSpeed) {
            20 -> definition.speed20ft
            30 -> definition.speed30ft
            else -> throw Exception("Speed undefined for $characterSpeed")
        }
    }
}