package com.mrmannwood.pathfinder

class LevelUpResult(
    val pathfinderClass: PathfinderClass,
    val hitDieResult: Int,
    val abilityScores: AbilityScores? = null,
    val skillRanks: Map<SkillDefinition, Int>,
    val grants: List<Grant> = emptyList(),
)

data class CharacterDef(
    val favoredClass: PathfinderClass,
    val size: Size,
    val levels: List<LevelUpResult>
)

data class Character(
    val characterDef: CharacterDef,
    val equippedEquipment: List<Equipment>,
    val unequippedEquipment: List<Equipment>,
    val abilityScoreModifiers: AbilityScores? = null,
) {

    companion object {
        private fun calculateLevels(levels: List<LevelUpResult>): Map<PathfinderClass, Int> {
            return levels
                .map { it.pathfinderClass }
                .groupBy { it }
                .map { it.key to it.value.size }
                .associate { it }
        }
        private fun getAllGrants(size: Size, equipment: List<Equipment>, levels: List<LevelUpResult>): List<Grant> {
            val grants = levels.flatMap { it.grants }.toMutableList()
            levels.map { it.pathfinderClass }.distinct().forEach { pathfinderClass ->
                for (level in 1 until levels.size) {
                    grants.addAll(pathfinderClass.grants(level))
                }
            }
            grants.add(size)
            equipment.forEach { grants.addAll(it.grants) }
            grants.add(object : Grant.GrantsArmorClassBonus {
                override val type = Grant.Type.INNATE
                override val name: String = "Base AC"
                override fun getArmorClassBonus() = Bonus(InnateBonus, "Rules") { 10 }
            })
            return grants
        }
        private fun calculateAbilityScores(levels: List<LevelUpResult>, modifiers: AbilityScores?): AbilityScores {
            val combineScores: (List<AbilityScores>, (AbilityScores) -> AbilityScore) -> AbilityScore = {
                    abilityScores, readScore ->
                val scores = abilityScores.mapNotNull {
                    val score = readScore(it)
                    if (score.getScore() == 0) null
                    else score.getActiveComponents().find { component -> component.type == ComponentType.INNATE }
                }.toMutableList()
                if (modifiers != null) {
                    val score = readScore(modifiers)
                    if (score.getScore() != 0) {
                        scores.add(
                            score.getActiveComponents().find { component -> component.type == ComponentType.TEMP_MODIFIER }!!
                        )
                    }
                }
                AbilityScore(scores)
            }
            return levels.mapNotNull { it.abilityScores }.let {
                AbilityScores(
                    strength = combineScores(it) { score -> score.strength },
                    dexterity = combineScores(it) { score -> score.dexterity },
                    constitution = combineScores(it) { score -> score.constitution },
                    intelligence = combineScores(it) { score -> score.intelligence },
                    wisdom = combineScores(it) { score -> score.wisdom },
                    charisma = combineScores(it) { score -> score.charisma }
                )
            }
        }
        private fun calculateHP(favoredClass: PathfinderClass, abilityScores: AbilityScores, levels: List<LevelUpResult>): List<Bonus> {
            return mutableListOf(
                Bonus(InnateBonus, "Con") { abilityScores.constitution.getModifier() * levels.size },
            ).apply {
                addAll(
                    levels.mapIndexed { idx, level ->
                        mutableListOf(
                            Bonus(InnateBonus, "Lvl ${idx + 1} HD Roll") { level.hitDieResult }
                        ).apply {
                            if ( level.pathfinderClass == favoredClass ) {
                                add(Bonus(InnateBonus, "Lvl ${idx + 1} Favored Class") { 1 })
                            }
                        }
                    }.flatten()
                )
            }
        }
        private fun calculateSaves(grants: List<Grant>, classLevels: Map<PathfinderClass, Int>): Saves {
            val saves = classLevels.asSequence().associate { it.key to it.key.baseSaves(it.value) }.map { it.key.name to it.value }
            val saveGrants = grants.filterIsInstance<Grant.GrantsSaveBonus>()
            return Saves(
                fortitudeSave = saves.map {
                    Bonus(InnateBonus, it.first) { it.second.fortitudeSave }
                } + saveGrants.map { it.getFortitudeSaveBonus() },
                reflexSave = saves.map {
                    Bonus(InnateBonus, it.first) { it.second.reflexSave }
                } + saveGrants.map { it.getReflexSaveBonus() },
                willSave = saves.map {
                    Bonus(InnateBonus, it.first) { it.second.willSave }
                } + saveGrants.map { it.getWillSaveBonus() },
            )
        }
        private fun calculateSkills(grants: List<Grant>, levels: List<LevelUpResult>): Map<Skill, List<Bonus>> {
            val skills = SKILL_DEFINITIONS.associateWith { mutableListOf<Bonus>() }.toMutableMap()
            levels.map { it.skillRanks }.forEachIndexed { idx, level ->
                level.forEach {
                    skills.getOrPut(it.key) { mutableListOf() }.add(Bonus(InnateBonus, "Level $idx") { it.value })
                }
            }
            grants.filterIsInstance<Grant.GrantsSkillRanks>().forEach { level ->
                level.getSkillRankGrants().forEach { grant ->
                    skills.getOrPut(grant.key) { mutableListOf() }.add(grant.value)
                }
            }
            val armorCheckPenalty = grants.filterIsInstance<Grant.GrantsArmorCheckPenalty>()
            if (armorCheckPenalty.isNotEmpty()) {
                armorCheckPenalty.forEach { grantsACPenalty ->
                    val penalties = grantsACPenalty.getSkillPenalties()
                    skills.forEach { (skill, bonuses) ->
                        penalties[skill]?.let { bonuses.add(it) }
                    }
                }
            }

            val classSkillGrants = mutableMapOf<SkillDefinition, MutableList<String>>()
            grants.filterIsInstance<Grant.GrantsClassSkills>().forEach { grant ->
                grant.getClassSkillGrants().forEach { skill ->
                    classSkillGrants.getOrPut(skill) { mutableListOf() }.add(grant.name)
                }
            }
            val classes = levels.map { it.pathfinderClass }.distinct()
            val classSkillCheck: (SkillDefinition) -> List<String>? = { skillDef ->
                (classes.filter { it.isClassSkill(skillDef) }.map { it.name } +
                        classSkillGrants.getOrElse(skillDef){ emptyList() }).ifEmpty { null }
            }
            return skills.map { Skill(it.key, it.key.name, classSkillCheck(it.key)) to it.value }.toMap()
        }
    }

    val classLevels: Map<PathfinderClass, Int> = calculateLevels(characterDef.levels)
    val grants: List<Grant> = getAllGrants(characterDef.size, equippedEquipment, characterDef.levels)
    val abilityScores = calculateAbilityScores(characterDef.levels, abilityScoreModifiers)
    val hp: List<Bonus> = calculateHP(characterDef.favoredClass, abilityScores, characterDef.levels)
    val baseAttackBonus = characterDef.favoredClass.baseAttackBonus(characterDef.levels.size)
    val saves = calculateSaves(grants, classLevels)
    val skills: Map<Skill, List<Bonus>> = calculateSkills(grants, characterDef.levels)
    val size: Size = characterDef.size

    fun maxHp(): Int = Bonus.getActiveBonuses(hp).sumOf { it.bonus() }

    val weapons: List<Weapon> = equippedEquipment.filterIsInstance<Weapon>()

    val armor: Armor? = equippedEquipment.filterIsInstance<Armor>().firstOrNull()
}

data class Saves(
    val fortitudeSave: List<Bonus>,
    val reflexSave: List<Bonus>,
    val willSave: List<Bonus>
)

data class BaseSaves(
    val fortitudeSave: Int,
    val reflexSave: Int,
    val willSave: Int
)
