package com.mrmannwood.pathfinder

object SizeBonus: Bonus.Type

sealed class Size(
    val modifier: Int,
    val specialModifier: Int,
    val flyModifier: Int,
    val stealthModifier: Int,
    val reach: Int,
): Grant.GrantsSkillRanks, Grant.GrantsAttackBonus, Grant.GrantsArmorClassBonus {

    object FINE: Size(8, -8, 8, 16, 0)
    object DIMINUTIVE: Size(4, -4, 6, 12, 0)
    object TINY: Size(2, -2, 4, 8, 0)
    object SMALL: Size(1, -1, 2, 4, 5)
    object MEDIUM: Size(0, 0, 0,0, 5)
    object LARGE_TALL: Size(-1, 1, -2, -4, 10)
    object LARGE_LONG: Size(-1, 1, -2, -4, 5)
    object HUGE_TALL: Size(-2, 2, -3, -4, 15)
    object HUGE_LONG: Size(-2, 2, -4, -8, 10)
    object GARGANTUAN_TALL: Size(-4, 4, -6, -12, 20)
    object GARGANTUAN_LONG: Size(-4, 4, -6, -12, 15)
    object COLOSSAL_TALL: Size(-8, 8, -8, -16, 30)
    object COLOSSAL_LONG: Size(-8, 8, -8, -16, 20)

    override val type = Grant.Type.SIZE
    override val name = "Size"
    override fun getSkillRankGrants(): Map<SkillDefinition, Bonus> {
        return mapOf(
            FLY to Bonus(SizeBonus, "Size") { flyModifier },
            STEALTH to Bonus(SizeBonus, "Size") { stealthModifier }
        )
    }

    override fun getAttackBonus(state: State): AttackBonusComponent {
        return AttackBonusComponent("Size") { modifier }
    }

    override fun getArmorClassBonus() = Bonus(SizeBonus, name) { modifier }
}