package com.mrmannwood.pathfinder

interface DieExpression {
    companion object {
        fun modifier(modifier: Int): DieExpression {
            return ModifierDieExpression(modifier)
        }
        fun singleDie(numRolls: Int, numSides: Int): DieExpression {
            return SingleDieExpression(numRolls, numSides)
        }
        fun parseExpression(expression: String): DieExpression {
            val result = mutableMapOf<Int, DieExpression>()
            for (exp in expression.split('+', '-')) {
                if (exp.trim() == "") continue
                val dp = exp.trim().split('d')
                if (dp.size == 1) {
                    val expr = ModifierDieExpression(dp[0].toInt())
                    result[0] = result[0]?.add(expr) ?: expr
                } else {
                    val expr = SingleDieExpression(dp[0].toInt(), dp[1].toInt())
                    result[expr.numSides] = result[expr.numSides]?.add(expr) ?: expr
                }
            }
            return CompositeDieExpression(
                result.entries
                    .sortedByDescending { it.key }
                    .map { it.value }
                    .toList()
            )
        }
    }

    fun add(expression: DieExpression): DieExpression
}

private data class ModifierDieExpression(val modifier: Int): DieExpression {

    override fun add(expression: DieExpression): DieExpression {
        return when (expression) {
            is ModifierDieExpression -> ModifierDieExpression(modifier + expression.modifier)
            else -> CompositeDieExpression(listOf(expression, this))
        }
    }

    override fun toString(): String = "$modifier"
}

private data class SingleDieExpression(val numRolls: Int, val numSides: Int): DieExpression {

    override fun add(expression: DieExpression): DieExpression {
        return when {
            expression is SingleDieExpression && numSides == expression.numSides->
                SingleDieExpression(expression.numRolls + numRolls, numSides)
            else -> CompositeDieExpression(listOf(expression, this))
        }
    }

    override fun toString(): String {
        return "${numRolls}d${numSides}"
    }
}

private data class CompositeDieExpression(private val components: List<DieExpression>): DieExpression {

    companion object {
        private fun addExpression(components: MutableList<DieExpression>, expression: DieExpression) {
            when (expression) {
                is ModifierDieExpression -> {
                    components.findLast { it::class == ModifierDieExpression::class }?.let {
                        components.remove(it)
                        components.add(it.add(expression))
                    } ?: run {
                        components.add(expression)
                    }
                }
                is SingleDieExpression -> {
                    components
                        .filterIsInstance<SingleDieExpression>()
                        .find { it.numSides == expression.numSides }
                        ?.let {
                            components.remove(it)
                            components.add(it.add(expression))
                        } ?: run {
                            components.add(expression)
                        }
                }
                is CompositeDieExpression -> {
                    for (component in expression.components) {
                        addExpression(components, component)
                    }
                }
            }
        }
    }

    override fun add(expression: DieExpression): DieExpression {
        val allComponents = components.toMutableList()
        addExpression(allComponents, expression)
        return CompositeDieExpression(
            allComponents.sortedWith { a, b ->
                when {
                    a is ModifierDieExpression -> 1
                    b is ModifierDieExpression -> -1
                    a is SingleDieExpression && b is SingleDieExpression -> {
                        b.numSides.compareTo(a.numSides)
                    }
                    else -> throw Exception("CompositeDieExpression cannot be sorted")
                }
            }
        )
    }

    override fun toString(): String {
        val sb = StringBuilder()
        components.forEachIndexed { i, component ->
            when (component) {
                is ModifierDieExpression -> {
                    if (i != 0 && component.modifier >= 0) {
                        sb.append("+")
                    }
                    sb.append(component.modifier)
                }
                is SingleDieExpression -> {
                    if (i != 0) { sb.append("+") }
                    sb.append("${component.numRolls}d${component.numSides}")
                }
                is CompositeDieExpression -> {
                    if (i != 0) { sb.append("+") }
                    sb.append(component.toString())
                }
            }
        }
        return sb.toString()
    }
}