package com.mrmannwood.pathfinder

import kotlin.js.Promise

class CharacterRepository(private val db: Any) {
    companion object {
        private var instance: CharacterRepository? = null
        fun get(db: Any): CharacterRepository {
            if (instance == null) {
                instance = CharacterRepository(db)
            }
            return instance!!
        }
    }

    fun readCharacter(): Promise<Character> {
        return Promise.all(arrayOf(
            readClass(db, "Archer"),
            readWeapon(db, "Longbow"),
            readWeapon(db, "Rapier"),
            readArmor(db, "Chain Shirt"),
        )).then { json ->
            val archer = PathfinderClass.fromJson(json[0])
            val longbow = WeaponDef.fromJson(json[1])
            val rapier = WeaponDef.fromJson(json[2])
            val chainShirt = ArmorDef.fromJson(json[3])
            Character(
                characterDef = CharacterDef(
                    favoredClass = archer,
                    size = Size.MEDIUM,
                    levels = listOf(
                        LevelUpResult(
                            pathfinderClass = archer,
                            hitDieResult = 10,
                            abilityScores = AbilityScores(
                                strength = AbilityScore(listOf(AbilityScoreComponent("character", ComponentType.INNATE, 14))),
                                dexterity = AbilityScore(listOf(AbilityScoreComponent("character", ComponentType.INNATE, 18))),
                                constitution = AbilityScore(listOf(AbilityScoreComponent("character", ComponentType.INNATE, 12))),
                                intelligence = AbilityScore(listOf(AbilityScoreComponent("character", ComponentType.INNATE, 12))),
                                wisdom = AbilityScore(listOf(AbilityScoreComponent("character", ComponentType.INNATE, 12))),
                                charisma = AbilityScore(listOf(AbilityScoreComponent("character", ComponentType.INNATE, 9)))
                            ),
                            skillRanks = mapOf(
                                BLUFF to 1,
                                SkillDefinition.defineSkill(CRAFT, "Calligraphy") to 1,
                                KNOWLEDGE_NOBILITY to 1,
                                SkillDefinition.defineSkill(PROFESSION, "Escort") to 1,
                                RIDE to 1,
                                STEALTH to 1,
                            ),
                            grants = listOf(SilentHunter, LesserNobility_HOUSE_RULE, WeaponFocus(longbow), POINT_BLANK_SHOT, WEAPON_FINESSE),
                        ),
                        LevelUpResult(
                            pathfinderClass = archer,
                            hitDieResult = 4,
                            skillRanks = mapOf(
                                ACROBATICS to 1,
                                KNOWLEDGE_NOBILITY to 1,
                                PERCEPTION to 1,
                                STEALTH to 1
                            ),
                            grants = listOf(PRECISE_SHOT)
                        ),
                        LevelUpResult(
                            pathfinderClass = archer,
                            hitDieResult = 10,
                            skillRanks = mapOf(
                                CLIMB to 1,
                                PERCEPTION to 1,
                                STEALTH to 1,
                                SWIM to 1
                            ),
                            grants = listOf(RAPID_SHOT)
                        ),
                    )
                ),
                equippedEquipment = listOf(
                    compositeLongbow(longbow, 2),
                    Weapon(rapier),
                    Armor(chainShirt, Size.MEDIUM)
                ),
                unequippedEquipment = emptyList()
            )
        }
    }
}