package com.mrmannwood.pathfinder

/* weight is given in 100s of a pound */
fun weightToPounds(weight: Int) = weight / 100

/* cost is given in copper */
fun costToString(cost: Int): String {
    val sb = StringBuilder()

    val cp = cost % 100
    var cost = cost / 100

    val sp = cost % 100
    cost = cost / 100

    val gp = cost

    if (gp > 0) {
        sb.append(gp).append("gp")
    }
    if (sp > 0) {
        if (sb.isNotEmpty()) sb.append(" ")
        sb.append(sp).append("sp")
    }
    if (cp > 0) {
        if (sb.isNotEmpty()) sb.append(" ")
        sb.append(cp).append("cp")
    }

    return sb.toString()
}