package com.mrmannwood.pathfinder

import kotlin.js.Json

data class PathfinderClass(
    val name: String,
    val link: String,
    val hitDie: DieExpression,
    val isClassSkill: (SkillDefinition) -> Boolean,
    val skillRanksPerLevel: Int,
    val baseAttackBonus: (level: Int) -> IntArray,
    val baseSaves: (level: Int) -> BaseSaves,
    val grants: (level: Int) -> List<Grant>
) {
    companion object {
        private fun parseBabs(babs: Array<Array<Int>>): (level: Int) -> IntArray {
            val baseAttackBonus = babs.map { it.toIntArray() }.toList()
            return { level -> baseAttackBonus[level - 1] }
        }
        private fun parseSaves(saves: Array<Array<Int>>): (level: Int) -> BaseSaves {
            val baseSaves = saves.map { BaseSaves(it[0], it[1], it[2]) }.toList()
            return { level -> baseSaves[level - 1] }
        }
        private fun parseClassFeatures(featuresByLevel: Array<Array<String>>): (level: Int) -> List<Grant> {
            // TODO
            return { level ->
                val classFeatures = featuresByLevel[level - 1]
                if (classFeatures.contains("hawkeye")) {
                    listOf(Hawkeye(1))
                } else {
                    emptyList()
                }
            }
        }
        fun fromJson(json: Json): PathfinderClass {
            val classSkills = (json["class_skills"] as Array<String>).map { SKILL_DEFINITIONS_MAP[it] }

            return PathfinderClass(
                name = json["name"] as String,
                link = json["link"] as String,
                hitDie = DieExpression.parseExpression(json["hd"] as String),
                isClassSkill = { classSkills.contains(it.prototype ?: it) },
                skillRanksPerLevel = json["skill_ranks"] as Int,
                baseAttackBonus = parseBabs(json["bab"] as Array<Array<Int>>),
                baseSaves = parseSaves(json["saves"] as Array<Array<Int>>),
                grants = parseClassFeatures(json["class_features"] as Array<Array<String>>)
            )
        }
    }
}
