package com.mrmannwood.pathfinder

object InnateBonus: Bonus.Type {
    override fun stacks() = true
}
object TraitBonus: Bonus.Type

enum class AbilityScoreBonus: Bonus.Type {
    STRENGTH,
    DEXTERITY,
    CONSTITUTION,
    INTELLIGENCE,
    WISDOM,
    CHARISMA
}

data class Bonus(val type: Type, val source: String, val bonus: () -> Int) {
    interface Type {
        fun stacks(): Boolean = false
    }

    companion object {
        fun getActiveBonuses(bonuses: List<Bonus>): List<Bonus> {
            val types = mutableMapOf<Type, MutableList<Bonus>>()
            bonuses.forEach {
                val component = types.getOrPut(it.type) { mutableListOf() }
                if (it.type.stacks() || component.isEmpty()) {
                    component.add(it)
                } else if (it.bonus() > component.first().bonus()) {
                    component[0] = it
                }
            }
            return types.flatMap { it.value }.toList()
        }
    }

}