package com.mrmannwood.pathfinder

import kotlinx.browser.*
import kotlinx.coroutines.GlobalScope
import kotlinx.coroutines.await
import kotlinx.coroutines.launch
import org.w3c.dom.*
import org.w3c.fetch.Response
import kotlin.js.Json
import kotlin.js.Promise

val modal by lazy { document.getElementById("modal") as HTMLDivElement }
val modalTableLabel by lazy { document.getElementById("modal_table_label") as HTMLLabelElement }
val modalTable by lazy { document.getElementById("modal_table") as HTMLTableElement }

suspend fun getFile(file: String): String {
    var response: Response? = null
    var numRetry = 0
    while (response == null) {
        try {
            response = window.fetch(file).await()
        } catch (e: Exception) {
            if (numRetry >= 3) {
                throw e
            }
            numRetry++
        }
    }
    return response.text().await()
}

external suspend fun openDatabase(): Promise<Array<Any>>
external suspend fun writeWeaponsToDatabase(db: Any, json: String): Promise<Unit>
external suspend fun writeArmorToDatabase(db: Any, json: String): Promise<Unit>
external suspend fun writeClassesToDatabase(db: Any, json: String): Promise<Unit>
external suspend fun readWeapons(db: Any): Promise<Array<String>>
external fun readWeapon(db: Any, weapon: String): Promise<Json>
external fun readArmor(db: Any, armor: String): Promise<Json>
external fun readClass(db: Any, pfClass: String): Promise<Json>

fun main() {
    window.onerror = { message, _, _, _, _ ->
        window.alert("A fatal error has occurred: $message")
    }
    window.addEventListener("click", { event ->
        if (event.target == modal) {
            modal.style.display = "none"
        }
    })

    lateinit var db: Any

    val navController = NavigationController(
        navBar = document.getElementById("nav_bar") as HTMLUListElement,
        vcDefs = listOf(
            NavigationController.ViewControllerDefinition(
                id = "nav_item_character_sheet",
                element = document.getElementById("character_sheet") as HTMLElement
            ) { CharacterSheetViewController(db) },
            NavigationController.ViewControllerDefinition(
                id = "nav_item_character_levels",
                element = document.getElementById("character_levels") as HTMLElement
            ) { CharacterLevelsViewController(db) },
        )
    )

    GlobalScope.launch {
        val openDbResult = openDatabase().await()
        db = openDbResult[0]
        println(openDbResult)
        val wasUpgraded = openDbResult[1] as Boolean
        if (wasUpgraded) {
            writeWeaponsToDatabase(db, getFile("weapons.json")).await()
            writeArmorToDatabase(db, getFile("armor.json")).await()
            writeClassesToDatabase(db, getFile("classes.json")).await()
        }
        (document.getElementById("nav_bar_box") as HTMLDivElement).style.display = "block"
        navController.navigateTo("nav_item_character_sheet")
    }
}
