package com.mrmannwood.pathfinder

data class State(
    val character: Character,
    val equippedWeapon: Weapon,
    val damage: Int,
    val attacks: IntArray,
    val inputs: Map<String, Any>
) {
    fun update(
        character: Character = this.character,
        equippedWeapon: Weapon = this.equippedWeapon,
        damage: Int = this.damage,
        attacks: IntArray = this.attacks,
        inputs: Map<String, Any> = this.inputs,
    ): State = State(
        character = character,
        equippedWeapon = equippedWeapon,
        damage = damage,
        attacks = attacks,
        inputs = inputs
    )
}
