package com.mrmannwood.pathfinder

import org.w3c.dom.HTMLElement

class NavigationController(
    navBar: HTMLElement,
    vcDefs: List<ViewControllerDefinition>
) {

    private var currentViewControllerDefinition: ViewControllerDefinition? = null
    private val viewControllers: Map<String, ViewControllerDefinition> = vcDefs.associateBy { it.id }

    init {
        navBar.addEventListener("click", { e ->
            if (e.target == null) return@addEventListener
            val target = e.target as HTMLElement
            navigateTo(target.id)
        })
    }

    fun navigateTo(id: String): Boolean {
        return viewControllers[id]?.let {
            if (it.viewController == null) {
                it.viewController = it.initialize()
            }
            if (it != currentViewControllerDefinition) {
                currentViewControllerDefinition?.let {
                    it.viewController?.onExit()
                    it.element.style.display = "none"
                }
            }
            currentViewControllerDefinition = it
            it.viewController!!.onEnter()
            it.element.style.display = "block"
            true
        } ?: run { false }
    }

    class ViewControllerDefinition(
        val id: String,
        val element: HTMLElement,
        val initialize: () -> ViewController
    ) {
        var viewController: ViewController? = null
    }
}