package com.mrmannwood.pathfinder

import kotlinx.browser.document
import org.w3c.dom.HTMLTableCellElement
import org.w3c.dom.HTMLTableElement
import org.w3c.dom.HTMLTableRowElement

class CharacterLevelsViewController(private val db: Any): ViewController {

    private val levelTable = document.getElementById("character_levels_table") as HTMLTableElement

    private val characterRepository = CharacterRepository.get(db)

    override fun onEnter() {
        characterRepository.readCharacter().then { character ->
            showCharacterLevels(character.characterDef.levels)
        }
    }

    override fun onExit() = Unit

    private fun showCharacterLevels(levels: List<LevelUpResult>) {
        while(levelTable.rows.length > 0) { levelTable.deleteRow(0) }
        levelTable.createTHead().let { header ->
            (header.insertRow(-1) as HTMLTableRowElement).let { row ->
                listOf(
                    "Class", "HD Roll", "Abilities", "Class Features", "Feats", "Skills", "Traits"
                ).forEach {
                    row.insertCell(-1).innerText = it
                }
            }
        }
        levels.forEachIndexed { idx, level ->
            levelTable.insertRow(-1).apply {
                insertCell(-1).innerHTML = makeLink(level.pathfinderClass.link, level.pathfinderClass.name)
                insertCell(-1).innerText = level.hitDieResult.toString()
                insertCell(-1).innerText = level.abilityScores?.let { scores ->
                    listOf(
                        "Strength" to scores.strength.getScore(),
                        "Dexterity" to scores.dexterity.getScore(),
                        "Constitution" to scores.constitution.getScore(),
                        "Intelligence" to scores.intelligence.getScore(),
                        "Wisdom" to scores.wisdom.getScore(),
                        "Charisma" to scores.charisma.getScore(),
                    )
                        .filter { it.second != 0 }
                        .joinToString("\n") { it.first + " " + it.second }
                } ?: run { "" }
                insertCell(-1).innerText = level.pathfinderClass.grants(idx + 1).filter { it.type == Grant.Type.CLASS_FEATURE }
                    .joinToString("\n") { it.name }
                insertCell(-1).innerText = level.grants.filter { it.type == Grant.Type.FEAT }
                    .joinToString("\n") { it.name }
                insertCell(-1).innerText = level.skillRanks
                    .filter { it.value != 0 }
                    .toList()
                    .joinToString("\n") { it.first.name + " " + it.second }
                insertCell(-1).innerText = level.grants.filter { it.type == Grant.Type.TRAIT }
                    .joinToString("\n") { it.name }
            }
        }
    }

    private fun makeLink(link: String, text: String): String {
        return """<a href="$link">$text</a>"""
    }
}