package com.mrmannwood.pathfinder

import kotlin.js.Json

class JsonParser(private val json: Json) {
    fun <T> parse(field: String, parse: (Any?) -> T): T {
        try {
            return parse.invoke(json[field])
        } catch (e: Exception) {
            println("Error occurred while parsing $field. Value = ${json[field]}. ${e.message}")
            throw e
        }
    }
}