package com.mrmannwood.pathfinder

import kotlin.js.Json
import kotlin.math.min

data class WeaponDef(
    val name: String,
    val cost: Int,
    val weight: Int,
    val damage: Map<Size, DieExpression>,
    val criticalRange: Int = 20,
    val criticalMultiplier: Int = 2,
    val type: Type,
    val rangeIncrement: Int? = null,
    val category: Category,
    val proficiency: Proficiency,
    val groups: List<Group>,
    val special: List<Special> = emptyList()
) {
    enum class Type {
        BLUDGEONING, PIERCING, SLASHING
    }
    enum class Category {
        LIGHT, ONE_HANDED, TWO_HANDED, RANGED
    }
    enum class Proficiency {
        SIMPLE, MARTIAL, EXOTIC
    }
    enum class Group {
        LIGHT_BLADES,
        HEAVY_BLADES,
        BOWS,
        FLAILS,
    }
    enum class Special {
        FINESSE,
        DISARM,
        NONLETHAL,
        REACH,
        TRIP
    }
    companion object {
        private fun unknownWeaponProperty(propertyName: String, propertyValue: Any?): Nothing {
            throw Exception("Unknown weapon $propertyName: $propertyValue")
        }
        private fun convertDamage(json: Json): Map<Size, DieExpression> {
            return mapOf(
                Size.SMALL to DieExpression.parseExpression(json["small"] as String),
                Size.MEDIUM to DieExpression.parseExpression(json["medium"] as String)
            )
        }
        private fun convertType(type: String): Type {
            val t = type.uppercase().first()
            return Type.values().firstOrNull { it.name.first() == t }
                ?: unknownWeaponProperty("type", type)
        }
        private fun convertCategory(category: String): Category {
            val c = category.uppercase()
            return Category.values().firstOrNull { it.name == c }
                ?: unknownWeaponProperty("category", category)
        }
        private fun convertProficiency(proficiency: String): Proficiency {
            val p = proficiency.uppercase()
            return Proficiency.values().firstOrNull { it.name == p }
                ?: unknownWeaponProperty("proficiency", proficiency)
        }
        private fun convertGroup(group: String): Group {
            val g = group.uppercase()
            return Group.values().firstOrNull { it.name == g }
                ?: unknownWeaponProperty("group", group)
        }
        private fun convertSpecial(special: String): Special {
            val s = special.uppercase()
            return Special.values().firstOrNull { it.name == s }
                ?: unknownWeaponProperty("special", special)
        }
        fun fromJson(json: Json): WeaponDef {
            return WeaponDef(
                name = json["name"] as String,
                cost = json["cost"] as Int,
                weight = json["weight"] as Int,
                damage = convertDamage(json["damage"] as Json),
                criticalRange = json["criticalRange"] as? Int ?: 20,
                criticalMultiplier = json["criticalMultiplier"] as? Int ?: 2,
                type = convertType(json["type"] as String),
                rangeIncrement = json["range"] as? Int,
                category = convertCategory(json["category"] as String),
                proficiency = convertProficiency(json["proficiency"] as String),
                groups = (json["groups"] as Array<String>).map { convertGroup(it) },
                special = (json["special"] as? Array<String>)?.map { convertSpecial(it) } ?: emptyList(),
            )
        }
    }
}

data class Weapon(
    val definition: WeaponDef,
    val size: Size = Size.MEDIUM,
    override val cost: Int = definition.cost,
    override val weight: Int = definition.weight,
    override val grants: List<Grant> = listOf()
): Equipment  {
    companion object {
        private fun computeName(definition: WeaponDef, grants: List<Grant>): String {
            return grants.filterIsInstance<Grant.GrantsNamePrefix>().joinToString(" ") { it.getPrefix() } +
                    " " + definition.name + " " +
                    grants.filterIsInstance<Grant.GrantsNameSuffix>().joinToString(" ") { it.getSuffix() }
        }
        private fun computeCriticalRange(definition: WeaponDef, grants: List<Grant>): Int {
            return grants.filterIsInstance<Grant.GrantsCriticalRange>()
                .map { it.addCriticalRange(definition.criticalRange) }
                .maxOfOrNull { it } ?: definition.criticalRange
        }
        private fun computeRangeIncrement(definition: WeaponDef, grants: List<Grant>): Int? {
            if (definition.rangeIncrement == null) return null
            return definition.rangeIncrement + grants.filterIsInstance<Grant.GrantsRangeIncrement>().sumOf { it.getRangeIncrementChange(definition) }
        }
    }
    override val name: String = computeName(definition, grants)
    val damage: DieExpression = definition.damage[size]!!
    val criticalRange = computeCriticalRange(definition, grants)
    val criticalMultiplier = definition.criticalMultiplier
    val type = definition.type
    val rangeIncrement = computeRangeIncrement(definition, grants)
    val category = definition.category
    val proficiency = definition.proficiency
    val groups = definition.groups
    val special = definition.special
    val ranged = category == WeaponDef.Category.RANGED
}

fun compositeLongbow(weaponDef: WeaponDef, rating: Int): Weapon {
    if (weaponDef.name.lowercase() != "longbow") {
        // TODO this is a pretty gross hack
        throw Exception("Only Longbows may be made composite")
    }
    return Weapon(
        definition = weaponDef,
        grants = listOf(
            object : Grant.GrantsNamePrefix, Grant.GrantsNameSuffix, Grant.GrantsAttackBonus, Grant.GrantsDamageBonus, Grant.GrantsRangeIncrement {
                override val type = Grant.Type.WEAPON
                override val name = "Composite Longbow"
                override fun getPrefix() = "Composite"
                override fun getSuffix() = "(+$rating)"
                override fun getAttackBonus(state: State): AttackBonusComponent? {
                    return if (state.character.abilityScores.strength.getModifier() >= rating) null else AttackBonusComponent("Strength Penalty") { -2 }
                }
                override fun getDamageBonus(state: State): DamageComponent {
                    return DamageComponent("Strength Bonus") {
                        DieExpression.modifier(min(rating, state.character.abilityScores.strength.getModifier()))
                    }
                }
                override fun getRangeIncrementChange(weapon: WeaponDef) = 10
            }
        )
    )
}

object MasterworkWeaponGrant : Grant, Grant.GrantsNamePrefix, Grant.GrantsAttackBonus {
    override val type = Grant.Type.WEAPON
    override val name = "Masterwork"
    override fun getPrefix() = "Masterwork"
    override fun getAttackBonus(state: State) = AttackBonusComponent(name) { 1 }
}

class EnhancedWeaponGrant(private val enhancement: Int) : Grant, Grant.GrantsNamePrefix, Grant.GrantsAttackBonus, Grant.GrantsDamageBonus {
    override val type = Grant.Type.WEAPON
    override val name = "Magical"
    override fun getPrefix() = "+$enhancement"
    override fun getAttackBonus(state: State) = AttackBonusComponent("Enhancement") { enhancement }
    override fun getDamageBonus(state: State) = DamageComponent("Enhancement") { DieExpression.modifier(enhancement) }
}

fun masterwork(weapon: Weapon): Weapon {
    if (weapon.grants.find { it == MasterworkWeaponGrant } != null) throw Exception("Weapon is already masterwork")
    return Weapon(
        definition = weapon.definition,
        grants = weapon.grants + MasterworkWeaponGrant
    )
}

fun enhanced(weapon: Weapon, enhancement: Int): Weapon {
    if (weapon.grants.filterIsInstance<EnhancedWeaponGrant>().isNotEmpty()) throw Exception("Weapon is already enhanced")
    if (weapon.grants.find { it == MasterworkWeaponGrant } == null) throw Exception("Weapon is not masterwork")
    return Weapon(
        definition = weapon.definition,
        grants = weapon.grants + EnhancedWeaponGrant(enhancement)
    )
}

val RANGE_INCREMENT_COMPONENT = AttackBonusComponent("Range") { state ->
    var rangeIncrement = state.equippedWeapon.rangeIncrement ?: return@AttackBonusComponent -4
    rangeIncrement += state.character.grants.filterIsInstance<Grant.GrantsRangeIncrement>().sumOf { it.getRangeIncrementChange(state.equippedWeapon.definition) }
    val distanceToTarget = state.inputs[InputNames.RANGE_TO_TARGET.displayName] as? Int ?: 0
    if (distanceToTarget <= rangeIncrement) return@AttackBonusComponent 0
    return@AttackBonusComponent -1 * (distanceToTarget - 1) / rangeIncrement
}

val MELEE_STRENGTH_COMPONENT = DamageComponent("Strength") { state ->
    val strength = state.character.abilityScores.strength.getModifier()
    if (state.equippedWeapon.category == WeaponDef.Category.LIGHT) return@DamageComponent DieExpression.modifier(strength)
//    if (!state.wieldingTwoHanded) return@DamageComponent DieExpression.modifier(strength)
    // TODO
    return@DamageComponent DieExpression.modifier((1.5 * strength).toInt())
}

class DamageComponent(val name: String, val bonus: (State) -> DieExpression?)

// TODO what about when they don't stack?
class AttackBonusComponent(val name: String, val bonus: (State) -> Int)