package com.mrmannwood.pathfinder

import kotlin.math.floor

data class AbilityScores(
    val strength: AbilityScore,
    val dexterity: AbilityScore,
    val constitution: AbilityScore,
    val intelligence: AbilityScore,
    val wisdom: AbilityScore,
    val charisma: AbilityScore
)

class AbilityScore(private val adjustments: List<AbilityScoreComponent>) {

    fun addComponent(component: AbilityScoreComponent): AbilityScore {
        return AbilityScore(adjustments + component)
    }

    fun getActiveComponents(): List<AbilityScoreComponent> {
        val types = mutableMapOf<ComponentType, MutableList<AbilityScoreComponent>>()
        adjustments.forEach {
            val component = types.getOrPut(it.type) { mutableListOf() }
            if (it.type.stack || component.isEmpty()) {
                component.add(it)
            } else if (it.score > component.first().score) {
                component[0] = it
            }
        }
        return types.flatMap { it.value }.toList()
    }

    fun getScore(): Int {
        return getActiveComponents().sumOf { it.score }
    }

    fun getModifier(): Int {
        return getScore().let { score ->
            if (score >= 10) {
                (score - 10) / 2
            } else {
                floor((score - 10) / 2.0).toInt()
            }
        }
    }
}

data class AbilityScoreComponent(val source: String, val type: ComponentType, val score: Int)

enum class ComponentType(val stack: Boolean) {
    INNATE(true),
    TEMP_MODIFIER(false)
}