package com.mrmannwood.pathfinder

// marker interface
interface Grant {
    enum class Type {
        INNATE,
        SIZE,
        TRAIT,
        FEAT,
        CLASS_FEATURE,
        WEAPON,
        ARMOR,
    }

    val type: Type
    val name: String
    fun optional(): Boolean = false

    interface GrantsNamePrefix: Grant {
        fun getPrefix(): String
    }

    interface GrantsNameSuffix: Grant {
        fun getSuffix(): String
    }

    interface GrantsSkillRanks: Grant {
        fun getSkillRankGrants(): Map<SkillDefinition, Bonus>
    }

    interface GrantsClassSkills: Grant {
        fun getClassSkillGrants(): List<SkillDefinition>
    }

    interface GrantsRangeIncrement: Grant {
        fun getRangeIncrementChange(weapon: WeaponDef): Int
    }

    interface GrantsCriticalRange: Grant {
        fun addCriticalRange(range: Int): Int
    }

    interface GrantsAttackBonus: Grant {
        fun getAttackBonus(state: State): AttackBonusComponent?
    }

    interface GrantsDamageBonus: Grant {
        fun getDamageBonus(state: State): DamageComponent?
    }

    interface ModifiesBaseAttackBonuses: Grant {
        fun modifyBabs(state: State): State
    }

    interface GrantsDexterityBonusInsteadOfStrengthOnAttack: Grant {
        fun shouldUseDex(state: State): Boolean
    }

    interface GrantsSaveBonus: Grant {
        fun getFortitudeSaveBonus(): Bonus
        fun getReflexSaveBonus(): Bonus
        fun getWillSaveBonus(): Bonus
    }

    interface GrantsMaxDexPenalty: Grant {
        fun getMaxDex(): Int
    }

    interface GrantsArmorCheckPenalty: Grant {
        fun getSkillPenalties(): Map<SkillDefinition, Bonus>
    }

    interface GrantsArmorClassBonus: Grant {
        fun getArmorClassBonus(): Bonus
    }
}

class Hawkeye(private val bonus: Int): Grant.GrantsSkillRanks, Grant.GrantsRangeIncrement {
    object HawkeyeBonus : Bonus.Type

    override val type = Grant.Type.CLASS_FEATURE
    override val name = "Hawkeye $bonus"
    override fun getSkillRankGrants() = mapOf(PERCEPTION to Bonus(HawkeyeBonus, name) { bonus } )
    override fun getRangeIncrementChange(weapon: WeaponDef) = if (weapon.groups.contains(WeaponDef.Group.BOWS)) bonus * 5 else 0
}

val SilentHunter: Grant = object : Grant.GrantsSkillRanks, Grant.GrantsClassSkills {
    override val type = Grant.Type.TRAIT
    override val name = "Silent Hunter"
    override fun getSkillRankGrants() = mapOf(STEALTH to Bonus(TraitBonus, name) { 1 })
    override fun getClassSkillGrants() = listOf(STEALTH)
}

val LesserNobility_HOUSE_RULE: Grant = object : Grant.GrantsSkillRanks, Grant.GrantsClassSkills {
    override val type = Grant.Type.TRAIT
    override val name = "Lesser Nobility"
    override fun getSkillRankGrants() = mapOf(
        DIPLOMACY to Bonus(TraitBonus, name) { 1 },
        KNOWLEDGE_NOBILITY to Bonus(TraitBonus, name) { 1 }
    )
    override fun getClassSkillGrants() = listOf(KNOWLEDGE_NOBILITY)
}

class WeaponFocus(private val weaponDef: WeaponDef): Grant.GrantsAttackBonus {
    override val type = Grant.Type.FEAT
    override val name = "Weapon Focus (${weaponDef.name})"
    override fun getAttackBonus(state: State): AttackBonusComponent? {
        return if (state.equippedWeapon.definition != weaponDef) null else AttackBonusComponent(name) { 1 }
    }
}

val POINT_BLANK_SHOT: Grant = object : Grant.GrantsAttackBonus, Grant.GrantsDamageBonus {
    override val type = Grant.Type.FEAT
    override val name = "Point Blank Shot"

    override fun getAttackBonus(state: State): AttackBonusComponent? {
        return if (!isApplicable(state)) null else AttackBonusComponent("Point Blank Shot") { 1 }
    }

    override fun getDamageBonus(state: State): DamageComponent? {
        return if (!isApplicable(state)) null else DamageComponent("Point Blank Shot") { DieExpression.modifier(1) }
    }

    private fun isApplicable(state: State): Boolean {
        return state.equippedWeapon.category == WeaponDef.Category.RANGED &&
                (state.inputs[InputNames.RANGE_TO_TARGET.displayName] as? Int ?: 0) <= 30
    }
}

