val RAPID_SHOT: Grant = object : Grant, Grant.GrantsAttackBonus, Grant.ModifiesBaseAttackBonuses {
    override val type = Grant.Type.FEAT
    override val name = "Rapid Shot"
    override fun optional() = true

    override fun getAttackBonus(state: State): AttackBonusComponent? {
        return if (!isApplicable(state)) null else AttackBonusComponent("Rapid Shot") { -2 }
    }

    override fun modifyBabs(state: State): State {
        return if (!isApplicable(state)) state else state.update(
            attacks = state.attacks.toMutableList().also { it.add(0, it[0]) }.toIntArray()
        )
    }

    private fun isApplicable(state: State): Boolean {
        return state.equippedWeapon.category == WeaponDef.Category.RANGED &&
                state.inputs[name] as? Boolean ?: false
    }
}