package com.mrmannwood.pathfinder

data class Skill(
    val definition: SkillDefinition,
    val name: String = definition.name,
    val classSkillSource: List<String>?
) {
    val abilityName = definition.abilityName
    val requiresTraining = definition.requiresTraining
    val isPrototype = definition.isPrototype
}

data class SkillDefinition(
    val name: String,
    val abilityName: String,
    val requiresTraining: Boolean = false,
    val isPrototype: Boolean = false,
    val prototype: SkillDefinition? = null
) {
    companion object {
        fun defineSkill(prototype: SkillDefinition, skill: String): SkillDefinition {
            if (!prototype.isPrototype) throw Exception("Skill ${prototype.name} is not a prototype")
            return SkillDefinition(
                name = "${prototype.name} $skill",
                abilityName = prototype.abilityName,
                requiresTraining = prototype.requiresTraining,
                isPrototype = false,
                prototype = prototype
            )
        }
    }
}

